import React from "react";
import s from "./FormsControl.module.css"
import {useFormik} from "formik";

const validate = values => {
    const errors = {};
    if (values.newMessageBody.length >
        15) {
        errors.newMessageBody = 'Must be 15 characters or less';
    }else if(!values.newMessageBody){
        errors.newMessageBody = "Required!";
    }
    return errors;
};

const AddMessageForm = (props) => {
    const formik = useFormik({
        initialValues: {
            newMessageBody: '',
        },
        validate,
        onSubmit: (data) => {
            let text = data.newMessageBody;
            props.sendMessage(text);
        }
    });
    return (
        <form className={s.message_bar} onSubmit={formik.handleSubmit}>
            <div className={s.formControl}>
                <textarea value={formik.values.newMessageBody}
                          onChange={formik.handleChange}
                          name={"newMessageBody"}
                          placeholder={"Enter your message"}>
                </textarea>
                {formik.errors.newMessageBody ? <div className={s.formSumError}>{formik.errors.newMessageBody}</div> : null}
            </div>
            <div>
                <button type={"submit"}>Send</button>
            </div>
        </form>
    )
}

export default AddMessageForm;

