import React from "react";
import s from "./FormsControl.module.css"
import {useFormik} from "formik";

const validate = values => {
    const errors = {};
    if (values.postsText.length > 30) {
        errors.postsText = 'Must be 30 characters or less';
    }else if(!values.postsText){
        errors.postsText = "Required!";
    }
    return errors;
};

const AddPostForm = (props) => {
    const formik = useFormik({
        initialValues: {
            postsText: '',
        },
        validate,
        onSubmit: (data) => {
            let text = data.postsText;
            props.addPost(text);
        }
    });
    return (
        <form className={s.message_bar} onSubmit={formik.handleSubmit}>
            <div className={s.formControl}>
                <textarea value={formik.values.postsText}
                          onChange={formik.handleChange}
                          name={"postsText"}
                          placeholder={"Enter your text"}>
                </textarea>
                {formik.errors.postsText ? <div className={s.formSumError}>{formik.errors.postsText}</div> : null}
            </div>
            <div>
                <button type={"submit"}>Post</button>
            </div>
        </form>
    )
}

export default AddPostForm;

