import React from "react";
import s from "./FormsControl.module.css"
import {useFormik} from "formik";
import {Field} from "redux-form";
import {Input} from "./AddMessageForm";
import {requiredField} from "../../../utils/validators/validators";

const validate = values => {
    const errors = {};
    if (values.email.length > 30) {
        errors.email = 'Must be 30 characters or less';
    }
    else if (values.password.length > 20) {
        errors.email = 'Must be 20 characters or less';
    }

    return errors;
};

const LoginForm = (props) => {
    const formik = useFormik({
        initialValues: {
            email: '',
            password:'',
            rememberMe:false,
        },
        validate,
        onSubmit: (data, submitProps) => {
            props.login(data.email, data.password, data.rememberMe, submitProps.setStatus)

        }
    });

    return (
        <form onSubmit={formik.handleSubmit}>
            <div className={s.formControl}>
                <div className={s.formControl + " " + (formik.touched.email && formik.values.email == "" ? s.error : null)}>
                    <input value={formik.values.email}
                           placeholder={"Email"}
                           name={"email"}
                           type={"email"}
                           onChange={formik.handleChange}
                           onBlur={formik.handleBlur}/>
                </div>
                <div className={s.formControl + " " + (formik.touched.password && formik.values.password == "" ? s.error : null)}>
                    <input value={formik.values.password}
                           placeholder={"Password"}
                           type={"password"}
                           name={"password"}
                           onChange={formik.handleChange}
                           onBlur={formik.handleBlur}/>
                </div>
                <div>
                    <input type={"checkbox"}
                           name={"rememberMe"}
                           onChange={formik.handleChange}
                    />
                </div>

                {formik.errors.email || formik.errors.password ? <div className={s.formSumError}>{formik.errors.email || formik.errors.password}</div> : null}
                {formik.status ? <div>{formik.status.error + ".   "}</div> : ""}
            </div>
            <div>
                <button type={"submit"}>Login</button>
            </div>
        </form>
    )
}

export default LoginForm;

