import React from "react";
import s from './Header.module.css';
import {NavLink} from "react-router-dom";



const Header = (props) => {
  return (
    <header className={s.header}>
      <img src="https://cdn.worldvectorlogo.com/logos/exmo.svg" />
        <div className={s.loginBlock}>
            {props.isAuth
                ? <div className={s.logout_block}><NavLink to={"/profile/"} className={s.nick}>{props.login}</NavLink>  <button onClick={props.logout} className={s.logout_btn}>Log out</button></div>
                : <NavLink to={'/login'} className={s.login}>Login</NavLink>}
        </div>
    </header>
  );
};



export default Header;